#resume0 {
  line-height: var(--r-line-height);
}
#resume0 .header {
  height: 5mm;
  background-color: var(--r-theme-color);
}
#resume0 .main {
  padding: 10mm;
}
#resume0 .base-info {
  display: flex;
  justify-content: space-between;
  height: 35mm;
}
#resume0 .school-logo {
  height: 13mm;
  width: 25mm;
}
#resume0 .personal-info {
  flex: 1 1;
  display: flex;
  align-items: center;
  flex-direction: column;
  font-size: var(--r-large-font-size);
  box-sizing: border-box;
  margin: 0 10mm;
}
#resume0 .personal-info .name {
  font-size: var(--r-large-font-size);
  font-weight: bold;
  margin-bottom: var(--r-line-margin);
}
#resume0 .personal-info .more {
  font-size: var(--r-small-font-size);
}
#resume0 .photo {
  height: 30mm;
  width: 25mm;
  background-color: var(--r-theme-color);
  font-size: var(--r-large-font-size);
  overflow: hidden;
  display: flex;
  align-items: center;
}
#resume0 .common-info {
  margin-top: var(--r-line-margin);
  font-size: var(--r-small-font-size);
  line-height: var(--r-line-height);
}
#resume0 .common-info .common-title {
  display: flex;
  align-items: center;
  font-size: var(--r-medium-font-size);
  font-weight: bold;
  color: var(--r-theme-color);
  border-left: 2mm solid var(--r-theme-color);
  padding: 1mm 4mm;
  background-color: var(--r-border-color);
  margin-bottom: var(--r-line-margin);
}
#resume0 .common-info .line-two-sides {
  display: flex;
  justify-content: space-between;
  margin-bottom: var(--r-line-margin);
}
#resume0 .common-info .line-normal {
  margin-bottom: var(--r-line-margin);
}
#resume0 .common-info .bold-text {
  font-weight: bold;
}
#edit0 {
  min-height: 900px;
}
#edit0 .edit-item,
#edit0 .edit-add-btn {
  min-height: 100px;
  background-color: #F7FAFF;
  margin-bottom: 4mm;
  border-radius: 2mm;
}
#edit0 .edit-add-btn {
  display: flex;
  align-items: center;
  justify-content: center;
}
